import React from "react";
import "./Header.css";
import { Link } from "react-router-dom";
import FacebookIcon from "@material-ui/icons/Facebook";
import InstagramIcon from "@material-ui/icons/Instagram";
import NotificationsNoneIcon from "@material-ui/icons/NotificationsNone";
import HelpOutlineIcon from "@material-ui/icons/HelpOutline";
import ShoppingCartOutlinedIcon from "@material-ui/icons/ShoppingCartOutlined";
import SearchIcon from "@material-ui/icons/Search";

function Header() {
  return (
    <nav className="header">
      <div className="header__navbar">
        <ul className="header__navbar__links__left">
          <a href="/">Seller Center</a>
          <a href="/">Seller Shopee</a>
          <a href="/">Download</a>
          <div>Follow us on</div>
          <div>
            <FacebookIcon className="header__FacebookIcon"></FacebookIcon>
            <InstagramIcon className="header__InstagramIcon"></InstagramIcon>
          </div>
        </ul>
        <ul className="header__navbar__links__right">
          <li>
            <NotificationsNoneIcon className="header__NotificationsIcon header__icons"></NotificationsNoneIcon>
            <span>Notifications</span>
          </li>
          <li>
            <HelpOutlineIcon className="header__HelpIcon header__icons"></HelpOutlineIcon>
            <span>Help</span>
          </li>
          <a href="/">Sign Up</a>
          <a href="/login">Login</a>
        </ul>
      </div>
      <div className="header__navbar__bottom">
        <a href="/">
          <img
            className="header__navbar__bottom__logo"
            src="https://liftoff.io/wp-content/uploads/2018/08/cs-shopee-logo-white.png"
            alt="shopee logo"
          ></img>
        </a>
        <div className="header__navbar__bottom__search">
          <div className="search__bar">
            <div className="search__bar__main">
              <input
                aria-label="Search for products, brands and shops"
                maxLength="999"
                placeholder="Search for products, brands and shops"
                autoComplete="off"
              ></input>
            </div>
            <button className="search__bar__button">
              <SearchIcon></SearchIcon>
            </button>
          </div>
          <div className="search__bar__bottom">
            <div className="search__bar__bottom__links">
              <a href="/">Study Table</a>
              <a href="/">Oversized Shirt</a>
              <a href="/">Seamless Pantry</a>
              <a href="/">Surgical Mask</a>
              <a href="/">Sexy Lingerie</a>
              <a href="/">Bathroom Organizer</a>
              <a href="/">Baking Pan</a>
              <a href="/">Skirt Coordinates</a>
            </div>
          </div>
        </div>
        <div className="header__navbar__bottom__cart">
          <ShoppingCartOutlinedIcon className="header__navbar__bottom__cart__icon"></ShoppingCartOutlinedIcon>
        </div>
      </div>
    </nav>
  );
}

export default Header;
