import "./Login.css";
import VisibilityIcon from "@material-ui/icons/Visibility";
import React from "react";
import FacebookIcon from "@material-ui/icons/Facebook";
import AppleIcon from "@material-ui/icons/Apple";

function Login() {
  return (
    <div className="login">
      <nav className="login__nav">
        <div className="login__nav__logo">
          <a href="/">
            <img src="https://www.freepnglogos.com/uploads/shopee-logo/logo-shopee-png-images-download-shopee-1.png" />{" "}
          </a>
          <h4>Login</h4>
        </div>
        <div className="login__nav__help">
          <h5>Need&nbsp;Help?</h5>
        </div>
      </nav>
      <section className="login__section">
        <div id="login__form">
          <form id="login__form__main">
            <div id="login__login">
              <h5>Login</h5>
            </div>
            <div id="login__form__input__1">
              <input placeholder="Phone number / Username / Email"></input>
            </div>
            <div id="login__form__input__2">
              <input placeholder="Password"></input>
              <VisibilityIcon class="eye__icon"></VisibilityIcon>
            </div>
            <div id="login__button">
              <button>Login</button>
            </div>
            <div id="login__links">
              <a href="/">Forgot Password</a>
              <a href="/">Login with SMS</a>
            </div>
            <div id="login__or">
              <span></span> or <span></span>
            </div>
            <div id="login__socialmedia">
              <button>
                <FacebookIcon></FacebookIcon>Facebook
              </button>
              <button>
                <span id="g__span">
                  <h3 id="G">G</h3>
                </span>
                Google
              </button>
              <button>
                <AppleIcon></AppleIcon>Apple
              </button>
            </div>
            <div id="sign__up">
              New to Shopee?&nbsp; <span> Sign Up</span>
            </div>
          </form>
        </div>
      </section>
    </div>
  );
}

export default Login;
